@import url(https://fonts.googleapis.com/css?family=Montserrat&display=swap);
@import url(https://cdnjs.cloudflare.com/ajax/libs/font-awesome/4.7.0/css/font-awesome.min.css);
* {
  margin: 0px;
  padding: 0px;
}
/* A link that has not been visited */
a {
  text-decoration: none;
}
a:link {
  color: black;
}
/* A link that has been visited */
a:visited {
  color: black;
}
/* A link that is hovered on */
a:hover {
  color: black;
}
/* A link that is selected */
a:active {
  color: black;
}
html,
body {
  margin: 0;
  padding: 0;
  color: #000;
  font-size: 14px;
  line-height: 20px;
  background-image: url(/static/media/background.f796edb9.jpg);
  background-attachment: fixed;
  background-size: cover;
  font-family: "Montserrat", sans-serif;
}

.container {
  position: relative;
  display: flex;
  width: 90%;
  height: 100%;
  max-width: 1000px;
  min-height: 200px;
  margin-right: auto;
  margin-left: auto;
  flex-direction: column;
  justify-content: flex-start;
  align-items: stretch;
}

.section {
  height: 100vh;
}
.projects-section {
  min-height: 100vh !important;
  height: auto !important;
  background-color: #fff !important;
  padding-top: 30px;
  padding-bottom: 30px;
}
.heading-2 {
  color: #000;
  font-size: 40px;
}
.greeting-title {
  color: #2b5ce4;
  font-size: 50px;
  letter-spacing: 2px;
}
.section-title {
  font-size: 50px;
  line-height: 50px;
  font-weight: 600;
  padding-left: 60px;
}

/* tablet */
@media (max-width: 991px) {
  .greeting-title {
    font-size: 40px;
  }
  .heading-2 {
    font-size: 30px;
  }
}
/* phone landscape */
@media (max-width: 767px) {
  .greeting-title {
    font-size: 35px;
    padding-right: 10px;
  }
  .heading-2 {
    font-size: 25px;
  }
}
/* phone */
@media (max-width: 479px) {
  .greeting-title {
    margin-top: 17px;
    font-size: 35px;
    text-align: right;
    letter-spacing: 2px;
    text-decoration: none;
  }
  .heading-2 {
    font-size: 25px;
  }
}

.home-left {
  position: relative;
  left: auto;
  top: 120px;
  bottom: 120px;
  max-width: 550px;
  padding-left: 0;
  align-self: flex-start;
  z-index: 1;
}

.bar {
  width: 80px;
  height: 2px;
  margin-top: 50px;
  margin-bottom: 50px;
  padding-top: 0px;
  padding-bottom: 0px;
  background-color: #000;
}

.section .home-section {
  display: flex;
  height: 100%;
  padding-top: 42px;
  padding-bottom: 42px;
  flex-direction: row;
}
.home-right {
  position: absolute;
  left: auto;
  top: auto;
  right: 0px;
  bottom: auto;
  width: 800px;
  height: 100%;
  border-radius: 20px;
  background-color: #ebebeb;
}

.home-image {
  position: absolute;
  width: 100%;
  height: 100%;
  border-radius: 20px;
  background-image: url(/static/media/christian.40cf2215.jpg);
  background-position: 50% 50%;
  background-size: cover;
  opacity: 0.3;
  -webkit-filter: contrast(167%) blur(1px);
          filter: contrast(167%) blur(1px);
  color: #f5c5c5;
}

@media (max-width: 767px) {
  .bar {
    display: none;
  }
  .section.home-section {
    display: block;
    overflow: hidden;
    height: 500px;
    padding-top: 0px;
    padding-bottom: 0px;
  }
  .home-right {
    right: -6%;
  }
  .home-image {
    display: flex;
    overflow: hidden;
    border-radius: 0px;
  }
}

@media (max-width: 479px) {
  .bar {
    display: none;
  }
  .section.home-section {
    height: 500px;
  }
  .home-right {
    height: 400px;
  }
  .home-image {
    overflow: hidden;
    background-position: 50% 50%;
    border-radius: 0px;
  }
}

.greeting-section {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: 1fr;
  grid-gap: 0rem;
  grid-template-areas: "left right";
  height: 100%;
}

.greeting-left {
  grid-area: left;
  display: flex;
  align-content: center;
  justify-content: center;
}

.greeting-right {
  grid-area: right;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  justify-content: center;
  line-height: 50px;
}

.greeting-image {
  width: 400px;
  border-radius: 100%;
  margin: auto;
  -webkit-filter: contrast(120%) blur(0.3px);
          filter: contrast(120%) blur(0.3px);
}
.icons {
  text-decoration: none;
  margin-left: 50px;
}

a.icon {
  margin-left: 10px;
  margin-right: 10px;
  color: dimgray;
  font-size: 60px;
  -webkit-transition: 3s;
  transition: 3s;
}
a.icon :hover {
  color: lightgray;
}
a.icon :active {
  font-size: 50px;
}
/* tablet */
@media (max-width: 991px) {
}
/* phone landscape */
@media (max-width: 767px) {
  .greeting-image {
    width: 250px;
  }
}
/* phone */
@media (max-width: 479px) {
  .greeting-section {
    display: flex;
    flex-direction: column;
    justify-content: center;
  }

  .greeting-image {
    width: 500px;
    border-radius: 0px;
  }
}

.project {
  margin-top: 70px;
  margin-bottom: 70px;
}
.project-image {
  height: 400px;
  background-size: cover;
  position: relative;
  border-radius: 10px;
  border: 0.5px solid gray;
}

.project-number-holder {
  position: absolute;
  right: 0;
  top: 0;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  margin-top: -50px;
  margin-right: 40px;
}
.project-number {
  font-size: 100px;
  font-weight: 100;
  line-height: 90px;
  -webkit-transform: skew(20deg);
          transform: skew(20deg);
  border-right: 3px solid black;
}

.tech-used {
  list-style-type: none;
  text-align: left;
  font-size: 20px;
  line-height: 20px;
  font-weight: bold;
  padding: 20px;
  -webkit-transform: skew(20deg);
          transform: skew(20deg);
}
.parallelogram {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  margin-right: -80px;
  width: auto;
  height: auto;
  -webkit-transform: skew(-20deg);
          transform: skew(-20deg);
  background-color: #add8e6;
  padding-left: 10px;
  padding-right: 10px;
}
.project-preview {
  display: flex;
  justify-content: flex-start;
  position: relative;
}
.project-content {
  margin-top: -100px;
  margin-left: 50px;
  width: 400px;
  padding: 25px;
  background: #e0f7ff;
  border-radius: 10px;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}
.project-name {
  text-transform: uppercase;
  padding-left: 30px;
  margin-bottom: 20px;
  font-size: 30px;
  line-height: 30px;
  border-left: 2px solid black;
}
.project-description {
  color: #404040;
  font-size: 20px;
}
.project-link {
  margin-top: 25px;
  font-size: 25px;
  line-height: 25px;
  font-weight: bold;
  text-align: right;
}
.project-link:hover {
  text-decoration: underline;
}

/* tablet */
@media (max-width: 991px) {
  .project-image {
    background-size: cover;
  }

  .project-number {
    font-size: 90px;
    font-weight: 100;
    line-height: 80px;
  }

  .project-description {
    color: #404040;
    font-size: 15px;
  }
  .project-link {
    font-size: 20px;
    line-height: 20px;
  }
}
/* phone landscape */
@media (max-width: 767px) {
  .project-number-holder {
    position: absolute;
    right: 0;
    top: 0;
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    margin-top: -25px;
    margin-right: 40px;
  }
  .parallelogram {
    margin-right: -25px;
    padding-left: 10px;
    padding-right: 10px;
  }
  .project-number {
    font-size: 60px;
    font-weight: 100;
    line-height: 50px;
  }

  .tech-used {
    font-size: 10px;
    line-height: 10px;
    padding: 10px;
  }
  .project-content {
    margin-top: 0px;
    margin-left: 0px;
    width: 90%;
  }
  .project-image {
    border-bottom-left-radius: 0px;
    border-bottom-right-radius: 0px;
  }
  .project-content {
    border-top-left-radius: 0px;
    border-top-right-radius: 0px;
  }
}
/* phone */
@media (max-width: 479px) {
}

