.project {
  margin-top: 70px;
  margin-bottom: 70px;
}
.project-image {
  height: 400px;
  background-size: cover;
  position: relative;
  border-radius: 10px;
  border: 0.5px solid gray;
}

.project-number-holder {
  position: absolute;
  right: 0;
  top: 0;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  margin-top: -50px;
  margin-right: 40px;
}
.project-number {
  font-size: 100px;
  font-weight: 100;
  line-height: 90px;
  transform: skew(20deg);
  border-right: 3px solid black;
}

.tech-used {
  list-style-type: none;
  text-align: left;
  font-size: 20px;
  line-height: 20px;
  font-weight: bold;
  padding: 20px;
  transform: skew(20deg);
}
.parallelogram {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  margin-right: -80px;
  width: auto;
  height: auto;
  transform: skew(-20deg);
  background-color: #add8e6;
  padding-left: 10px;
  padding-right: 10px;
}
.project-preview {
  display: flex;
  justify-content: flex-start;
  position: relative;
}
.project-content {
  margin-top: -100px;
  margin-left: 50px;
  width: 400px;
  padding: 25px;
  background: #e0f7ff;
  border-radius: 10px;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}
.project-name {
  text-transform: uppercase;
  padding-left: 30px;
  margin-bottom: 20px;
  font-size: 30px;
  line-height: 30px;
  border-left: 2px solid black;
}
.project-description {
  color: #404040;
  font-size: 20px;
}
.project-link {
  margin-top: 25px;
  font-size: 25px;
  line-height: 25px;
  font-weight: bold;
  text-align: right;
}
.project-link:hover {
  text-decoration: underline;
}

/* tablet */
@media (max-width: 991px) {
  .project-image {
    background-size: cover;
  }

  .project-number {
    font-size: 90px;
    font-weight: 100;
    line-height: 80px;
  }

  .project-description {
    color: #404040;
    font-size: 15px;
  }
  .project-link {
    font-size: 20px;
    line-height: 20px;
  }
}
/* phone landscape */
@media (max-width: 767px) {
  .project-number-holder {
    position: absolute;
    right: 0;
    top: 0;
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    margin-top: -25px;
    margin-right: 40px;
  }
  .parallelogram {
    margin-right: -25px;
    padding-left: 10px;
    padding-right: 10px;
  }
  .project-number {
    font-size: 60px;
    font-weight: 100;
    line-height: 50px;
  }

  .tech-used {
    font-size: 10px;
    line-height: 10px;
    padding: 10px;
  }
  .project-content {
    margin-top: 0px;
    margin-left: 0px;
    width: 90%;
  }
  .project-image {
    border-bottom-left-radius: 0px;
    border-bottom-right-radius: 0px;
  }
  .project-content {
    border-top-left-radius: 0px;
    border-top-right-radius: 0px;
  }
}
