.home-left {
  position: relative;
  left: auto;
  top: 120px;
  bottom: 120px;
  max-width: 550px;
  padding-left: 0;
  align-self: flex-start;
  z-index: 1;
}

.bar {
  width: 80px;
  height: 2px;
  margin-top: 50px;
  margin-bottom: 50px;
  padding-top: 0px;
  padding-bottom: 0px;
  background-color: #000;
}

.section .home-section {
  display: flex;
  height: 100%;
  padding-top: 42px;
  padding-bottom: 42px;
  flex-direction: row;
}
.home-right {
  position: absolute;
  left: auto;
  top: auto;
  right: 0px;
  bottom: auto;
  width: 800px;
  height: 100%;
  border-radius: 20px;
  background-color: #ebebeb;
}

.home-image {
  position: absolute;
  width: 100%;
  height: 100%;
  border-radius: 20px;
  background-image: url("../../images/christian.jpg");
  background-position: 50% 50%;
  background-size: cover;
  opacity: 0.3;
  filter: contrast(167%) blur(1px);
  color: #f5c5c5;
}

@media (max-width: 767px) {
  .bar {
    display: none;
  }
  .section.home-section {
    display: block;
    overflow: hidden;
    height: 500px;
    padding-top: 0px;
    padding-bottom: 0px;
  }
  .home-right {
    right: -6%;
  }
  .home-image {
    display: flex;
    overflow: hidden;
    border-radius: 0px;
  }
}

@media (max-width: 479px) {
  .bar {
    display: none;
  }
  .section.home-section {
    height: 500px;
  }
  .home-right {
    height: 400px;
  }
  .home-image {
    overflow: hidden;
    background-position: 50% 50%;
    border-radius: 0px;
  }
}
