.greeting-section {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: 1fr;
  grid-gap: 0rem;
  grid-template-areas: "left right";
  height: 100%;
}

.greeting-left {
  grid-area: left;
  display: flex;
  align-content: center;
  justify-content: center;
}

.greeting-right {
  grid-area: right;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  justify-content: center;
  line-height: 50px;
}

.greeting-image {
  width: 400px;
  border-radius: 100%;
  margin: auto;
  filter: contrast(120%) blur(0.3px);
}
.icons {
  text-decoration: none;
  margin-left: 50px;
}

a.icon {
  margin-left: 10px;
  margin-right: 10px;
  color: dimgray;
  font-size: 60px;
  transition: 3s;
}
a.icon :hover {
  color: lightgray;
}
a.icon :active {
  font-size: 50px;
}
/* tablet */
@media (max-width: 991px) {
}
/* phone landscape */
@media (max-width: 767px) {
  .greeting-image {
    width: 250px;
  }
}
/* phone */
@media (max-width: 479px) {
  .greeting-section {
    display: flex;
    flex-direction: column;
    justify-content: center;
  }

  .greeting-image {
    width: 500px;
    border-radius: 0px;
  }
}
