@import url("https://fonts.googleapis.com/css?family=Montserrat&display=swap");
@import url("https://cdnjs.cloudflare.com/ajax/libs/font-awesome/4.7.0/css/font-awesome.min.css");

* {
  margin: 0px;
  padding: 0px;
}
/* A link that has not been visited */
a {
  text-decoration: none;
}
a:link {
  color: black;
}
/* A link that has been visited */
a:visited {
  color: black;
}
/* A link that is hovered on */
a:hover {
  color: black;
}
/* A link that is selected */
a:active {
  color: black;
}
html,
body {
  margin: 0;
  padding: 0;
  color: #000;
  font-size: 14px;
  line-height: 20px;
  background-image: url("./images/background.jpg");
  background-attachment: fixed;
  background-size: cover;
  font-family: "Montserrat", sans-serif;
}

.container {
  position: relative;
  display: flex;
  width: 90%;
  height: 100%;
  max-width: 1000px;
  min-height: 200px;
  margin-right: auto;
  margin-left: auto;
  flex-direction: column;
  justify-content: flex-start;
  align-items: stretch;
}

.section {
  height: 100vh;
}
.projects-section {
  min-height: 100vh !important;
  height: auto !important;
  background-color: #fff !important;
  padding-top: 30px;
  padding-bottom: 30px;
}
.heading-2 {
  color: #000;
  font-size: 40px;
}
.greeting-title {
  color: #2b5ce4;
  font-size: 50px;
  letter-spacing: 2px;
}
.section-title {
  font-size: 50px;
  line-height: 50px;
  font-weight: 600;
  padding-left: 60px;
}

/* tablet */
@media (max-width: 991px) {
  .greeting-title {
    font-size: 40px;
  }
  .heading-2 {
    font-size: 30px;
  }
}
/* phone landscape */
@media (max-width: 767px) {
  .greeting-title {
    font-size: 35px;
    padding-right: 10px;
  }
  .heading-2 {
    font-size: 25px;
  }
}
/* phone */
@media (max-width: 479px) {
  .greeting-title {
    margin-top: 17px;
    font-size: 35px;
    text-align: right;
    letter-spacing: 2px;
    text-decoration: none;
  }
  .heading-2 {
    font-size: 25px;
  }
}
